export default function querySelectorContainsNode(selector: string, node: any) {
    var candidates = document.querySelectorAll(selector);
    var i: number;
    var l = candidates.length;
    for (i = 0; i < l; i++) {
      if (candidates[i] === node || candidates[i].contains(node)) {
        return true;
      }
    }
    return false;
  };